html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10px;
}

body {
  max-width: 1200px;
  background-color: #f7f589;
  border: 3px solid #32a4e6;
  margin: 0 auto;
  padding: 0 20px 20px;
}

.header__title {
  text-align: center;
  color: #00f;
  text-shadow: 3px 3px 1px #000;
  margin: 0;
  padding: 20px 0;
  font-size: 60px;
}

.main__title {
  text-align: center;
  color: #000;
  text-shadow: 3px 3px 2px #fe0;
  margin: 0;
  padding: 20px 0;
  font-size: 40px;
}

.main__description, .main__list-item {
  letter-spacing: 1px;
  font-size: 16px;
  line-height: 2;
}

.main__img, .main__video {
  max-width: 40%;
  min-width: 250px;
  margin: 1px auto;
  display: block;
}

.menu {
  background-color: #f5f108f8;
  border: 1px solid #32a4e6;
  justify-content: space-around;
  padding: 8px;
  font-size: 20px;
  list-style-type: none;
  display: flex;
}

@media (max-width: 600px) {
  .menu {
    flex-direction: column;
  }
}

.menu__item {
  background: red;
  padding: 8px;
}

.menu__item a {
  text-decoration: none;
}

.gallery {
  text-align: center;
}

.gallery__item {
  width: 33%;
  display: inline-block;
}

@media (max-width: 600px) {
  .gallery__item {
    width: 45%;
  }
}

.gallery__item-img {
  max-width: 100%;
}

@media screen and (max-width: 800px) {
  .gallery__item {
    width: 40%;
  }
}

